import { Typography, Box, Stack } from "@mui/material";
import { purple } from "../Constants";
import penguin from "../assets/penguin.png";

export const Top = () => {
  return (
    <Box
      sx={{
        backgroundColor: purple,
        color: "white",
        minHeight: "100vh",
        display: "flex",
        alignItems: "center",
        // justifyContent: "center",
      }}
    >
      <Stack direction="row" spacing={10}>
        <Box
          component="img"
          src={penguin}
          alt="penguin"
          sx={{
            width: "400px",
            height: "auto",
            objectFit: "contain",
          }}
        />
        <Stack spacing={5}>
          <Typography variant="h2" sx={{ paddingTop: "100px" }}>
            Yuto Abe /  
          </Typography>
          <Box>
            <div>  </div>
            <br/>
            <div>
               
            </div>
            <div>
              
            </div>
            <br/>
            <div>
              WEB
            </div>
            <div>
               ReactTypeScript Scala
            </div>
            <br />
            <div></div>
            <div></div>
          </Box>
        </Stack>
      </Stack>
    </Box>
  );
};
